.flag-form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 1rem;

  justify-content: center;
  text-align: left;
  font-size: 1.2em;
}

@media screen and (min-width: 768px) {
  .flag-form {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }
}
